body {
  background: rgba(0, 0, 0, .1) !important;
}

footer.main-footer {
  color: #6c757d;
  text-align: center;
  font-size: 12px;
  margin-top: 20px;
}

.timeline-card {
  margin-bottom: 20px;
}

button .github-logo {
  float: left;
}

.login-modal-container {
  padding: 60px 40px 60px 40px;
}

.login-modal-container h2 {
  font-size: 20px;
}

.login-modal-container p {
  font-size: 12px;
}

.login-modal-container button {
  width: 100%;
}

.markdown-body a {
  text-decoration: none;
  color: #0266b3;
}

.markdown-body pre {
  padding: 16px;
  overflow: auto;
  font-size: 80%;
  line-height: 1.45;
  background-color: #f4f4f4;
  border-radius: 6px;
}

.markdown-body pre code,
.markdown-body pre tt {
  display: inline;
  max-width: auto;
  padding: 0;
  margin: 0;
  overflow: visible;
  line-height: inherit;
  word-wrap: normal;
  background-color: transparent;
  border: 0;
}

.markdown-body code br,
.markdown-body tt br {
  display: none;
}

.markdown-body blockquote {
  border-left: 4px solid #e4e4e4;
  margin: 10px 0 10px 8px;
  padding-left: 8px;
  color: #6c757d;
}

.markdown-body hr {
  margin: 20px;
  color: #6c757d;
}

.markdown-body ul,
.markdown-body ol {
  padding-left: 2em;
}

.markdown-body ul>li,
.markdown-body ol>li {
  margin-left: 0;
}

.discussion-body p {
  margin-top: 0;
  margin-bottom: 16px;
  line-height: 1.6em;
}

.discussion-body img {
  width: 100%;
}

.discussion-comment-body p {
  margin-block-start: 0;
  margin-block-end: 0;
  line-height: 1.4em;
}

.discussion-comment-body img {
  max-height: 240px;
  width: auto;
}
